@charset 'utf-8';

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  // height: 28px;
  // margin: 14px 0 12px;
  margin: 45px 60px;
  line-height: normal;
  text-align: center;
  // font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 13px 13px 11px 13px;
  margin: 0;
  color: $txtColorDark;
  font-family: $mainFont;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
  border-radius: 5px;
  vertical-align: middle;
  // border: 1px solid #ddd;
  // border-width: 1px 0;
}

.tui-pagination .tui-page-btn:hover {
  background-color: #f4f4f4;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: $txtColorWhite;
  background: $genreColor;
  border-radius: 5px;
  // border-color: #fff;
  cursor: pointer;
}

.tui-pagination .tui-is-selected:hover {
  background-color: #ec9f0f;
}

